import { Component, OnInit ,OnDestroy } from '@angular/core';
import {ContentListComponent} from "./content-list/content-list.component";
import {EditContentComponent} from "./edit-content/edit-content.component";
import {ContentService} from "../content/content.service";
import {Content } from "../shared/content";
import {ActivatedRoute} from '@angular/router';
import {Subscription} from "rxjs/Rx";


@Component({
  moduleId: module.id,
  selector: 'app-content',
  templateUrl: 'content.component.html',
  styleUrls: ['content.component.css'],
  directives: [ContentListComponent , EditContentComponent]

})
export class ContentComponent implements OnInit , OnDestroy{

  private subscription: Subscription;
  articles : Content[] = [];

  constructor(private contentService : ContentService ,
              private activRouter: ActivatedRoute) {}

  ngOnInit() {

    var section : string;
    this.subscription = this.activRouter.params.subscribe(
      (param : any) => {
        section = param['section'];
        /// WIll DELETE THIS LOGIC DOES NOT WORK//////////////
        if( section == 'DataBase'){
          this.articles = [];
          var arr : Content[] = this.contentService.getContent();
          for(let i = 0 ; i < arr.length; i++){
            for(let j = 0 ; j < arr[i].sections.length; j++){
              var str : string = arr[i].sections[j];
              console.log(str);
              if (arr[i].sections[j] == 'DataBase'){
                console.log(arr[i].sections[j]);

                this.articles.push(arr[i]);
              }
            }
          }
        }
        else{
          this.articles = this.contentService.getContent();
          console.log('hi');
        }
      });
    ///////////////////////////////////////
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}
